.datatable {
  height: 50rem;
  padding: 2rem;
  .heading {
    color: #343;
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }
  .pending-orders {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: green;
    font-weight: bold;
    font-size: 2rem;
  }
  .datagrid {
    font-size: 1.3rem;
    p {
      font-size: 1.2rem;
    }
    .datatableTitle {
      width: 100%;
      font-size: 2.4rem;
      color: gray;
      margin-bottom: 1rem;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .link {
        text-decoration: none;
        color: green;
        font-size: 16px;
        font-weight: 400;
        border: 1px solid green;
        padding: 5px;
        border-radius: 5px;
        cursor: pointer;
      }
    }
    .price {
      color: gray;
    }
    .cellWithImg {
      display: flex;
      align-items: center;

      .cellImg {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 20px;
      }
    }

    .cellWithStatus {
      padding: 5px;
      border-radius: 5px;

      &.active {
        background-color: rgba(0, 128, 0, 0.05);
        color: green;
      }
      &.pending {
        background-color: rgba(255, 217, 0, 0.05);
        color: goldenrod;
      }
      &.passive {
        background-color: rgba(255, 0, 0, 0.05);
        color: crimson;
      }
    }
    .status {
      padding: 0.2rem 0.8rem;
      font-size: 1.2rem;
      color: #fff;
      border-radius: 5px;
    }
    .status-active {
      background-color: rgba(0, 128, 0, 0.682);
    }
    .status-deactive {
      background-color: rgba(255, 0, 0, 0.719);
    }
    .cellAction {
      display: flex;
      align-items: center;
      gap: 15px;

      .viewButton {
        padding: 2px 5px;
        border-radius: 5px;
        color: darkblue;
        border: 1px dotted rgba(0, 0, 139, 0.596);
        cursor: pointer;
      }

      .deleteButton {
        padding: 2px 5px;
        border-radius: 5px;
        color: crimson;
        border: 1px dotted rgba(220, 20, 60, 0.6);
        cursor: pointer;
      }
    }
  }
}
